import { ValidatorBase } from "./ValidatorBase";

export class RequiredValidator extends ValidatorBase {
    // constructor(input?:any, fieldName?:string, fieldDisplayName?:string)
    // {
    //     super(input, fieldName, fieldDisplayName);
    // }
    validate(input?:any, fieldName?:string, fieldDisplayName?:string): boolean {
        if(this.input == null || this.input == "" || typeof(this.input) === typeof(undefined)) {
            return this.fail("is required");
        }
        return this.succeed();
    }
}